import Vue from 'vue'
import App from './App.vue'
import store from './store.js'

// 1. 导入 ant-design-vue 组件库以及样式表
import Antd from 'ant-design-vue'
import 'ant-design-vue/dist/antd.css'

Vue.config.productionTip = false
// 2. 安装组件库
Vue.use(Antd)

new Vue({
  render: (h) => h(App),
  store,
}).$mount('#app')
