<template>
  <div>
    <!-- footer区域 -->
    <div slot="footer" class="app-footer-css">
      <!-- 未完成的任务个数 -->
      <a-button-group>
        <a-button disabled>剩余<i style="color:red">{{ unDoneLength }}</i>条待完成</a-button>
      </a-button-group>
      <!-- 操作按钮 -->
      <a-button-group>
        <a-button :type="viewKey === 'all' ? 'primary' : 'default'" @click="changeList('all')">全部</a-button>
        <a-button :type="viewKey === 'undone' ? 'primary' : 'default'" @click="changeList('undone')">未完成</a-button>
        <a-button :type="viewKey === 'done' ? 'primary' : 'default'" @click="changeList('done')">已完成</a-button>
      </a-button-group>
      <!-- 把已经完成的任务清空 -->
      <a-button-group>
        <a-button @click="clean">清除已完成</a-button>
      </a-button-group>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapState } from 'vuex'

export default {
  computed: {
    ...mapState(['viewKey']),
    ...mapGetters(['unDoneLength']),
  },
  methods: {
    ...mapMutations(['cleanDone', 'changeViewKey',]),

    // 清除已完成的任务
    clean() {
      this.cleanDone()
    },

    // 修改页面上展示的列表数据
    changeList(key) {
      this.changeViewKey(key)
    },

  },
}
</script>

<style scoped>
.app-footer-css {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
