<template>
  <div>
    <!-- 待办列表 -->
    <a-list bordered :dataSource="infolist" class="app-list-css">
      <!-- 每一项待办 -->
      <a-list-item slot="renderItem" slot-scope="item">
        <!-- 复选框 -->
        <a-checkbox :checked="item.done" @change="(e) => { cbStatusChanged(e, item.id) }">
          <span :class="{ active: item.done }">{{ item.info }}</span>
        </a-checkbox>
        <!-- 删除链接 -->
        <a slot="actions" @click="removeItemById(item.id)">删除</a>
      </a-list-item>
    </a-list>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  created() {
    this.getList()
  },
  computed: {
    ...mapGetters(['infolist']),
  },
  methods: {
    ...mapMutations(['removeItem', 'changeStatus',]),
    ...mapActions(['getList']),

    // 根据Id删除对应的任务事项
    removeItemById(id) {
      this.removeItem(id)
    },

    // 监听复选框选中状态变化的事件
    cbStatusChanged(e, id) {
      const param = {
        id: id,
        status: e.target.checked,
      }
      this.changeStatus(param)
    },
  },
}
</script>

<style scoped>
.app-list-css {
  width: 100%;
}

span.active {
  font-style: italic;
  text-decoration: line-through #1890ff;
  color: #1890ff;
}
</style>
