<template>
  <div>
    <!-- 输入框 -->
    <a-input placeholder="请输入任务" :value="inputValue" @change="handleInputChange" class="app-input-css" />
    <!-- 添加按钮 -->
    <a-button type="primary" @click="addItemToList" class="app-button-css">添加事项</a-button>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState(['inputValue'])
  },
  methods: {
    ...mapMutations(['setInputValue', 'addItem']),

    // 监听文本框内容变化,将新值赋值给inputValue
    handleInputChange(e) {
      this.setInputValue(e.target.value)
    },

    // 向列表中新增 item 项
    addItemToList() {
      if (this.inputValue.trim().length <= 0) {
        return this.$message.warning('文本框内容不能为空！')
      }
      this.addItem()
    },
  },
}
</script>

<style scoped>
.app-input-css {
  margin-right: 10px;
}
</style>
